import { CSSTransition } from 'react-transition-group';

const Dialog = (props) => {
  const timeout = props.timeout !== undefined ? props.timeout : 300;
  let elem;
  const handleClose = (e) => {
    if (
      e.target === elem &&
      (props.backdropClose === true || props.backdropClose === 'true')
    ) {
      props.close();
    }
  };
  return (
    <>
      <CSSTransition
        in={props.open}
        appear={props.open}
        unmountOnExit
        timeout={timeout}
        classNames={props.classNames}
      >
        <div
          ref={(node) => {
            elem = node;
          }}
          className="dialog-container"
          style={
            props.backdropClose === true || props.backdropClose === 'true'
              ? { cursor: 'pointer', pointerEvents: 'all' }
              : {}
          }
          onClick={(e) => handleClose(e)}
        >
          <div className={`dialog ${props.classNames}`}>
            <div className="dialog-inner">{props.children}</div>
          </div>
        </div>
      </CSSTransition>
    </>
  );
};

export default Dialog;
