import React, { useState, useEffect, useRef } from 'react';
import { CSSTransition } from 'react-transition-group';
import { db, auth } from '../utils/firebase';

import Button from './Button';
import Input from './Input';

import { ReactComponent as LoginSvg } from '../assets/Welcome.svg';
import { ReactComponent as SignupSvg } from '../assets/signup.svg';
const UserForm = (props) => {
  const [formState, setFormState] = useState(props.currentForm);
  const [showPassword, setShowPassword] = useState(false);
  const [username, setUsername] = useState('');
  const [mail, setMail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [formErrorMessage, setFormErrorMessage] = useState('');

  const inp = useRef();
  const userColl = db.collection('users');

  useEffect(() => {
    inp.current.focus();
    if (formState === 'login' || formState === 'signup') {
      setFormErrorMessage('');
    }
  }, [formState]);

  const handleLoginSubmit = (e) => {
    e.preventDefault();
    if (password !== '' && username !== '') {
      userColl
        .where('username', '==', username.toLowerCase())
        .get()
        .then((docs) => {
          docs.forEach((doc) => {
            const data = doc.data();
            const email = data.email;
            const username = data.username;
            auth
              .signInWithEmailAndPassword(email, password)
              .then((credentials) => {
                setUsername('');
                setMail('');
                setPassword('');
                setConfirmPassword('');

                handleUIMessage(`logged you in, ${username}`);

                handleClose();
              })
              .catch((error) => {
                console.log(error);
                if (error.code === 'auth/wrong-password') {
                  setFormErrorMessage("Password invalid or user doesn't exist");
                } else if (error.code === 'auth/too-many-requests') {
                  setFormErrorMessage('Access denied (too many bad requests)');
                }
              });
          });
        });
    } else {
      setFormErrorMessage('please fill in all fields');
    }
  };
  const handleSignupSubmit = (e) => {
    e.preventDefault();
    if (
      password !== '' &&
      confirmPassword !== '' &&
      mail !== '' &&
      username !== '' &&
      password === confirmPassword
    ) {
      userColl
        .where('username', '==', username.toLowerCase())
        .get()
        .then((docs) => {
          if (docs.empty) {
            userColl
              .where('email', '==', mail.toLowerCase())
              .get()
              .then((docs) => {
                if (docs.empty) {
                  auth
                    .createUserWithEmailAndPassword(mail, password)
                    .then((credentials) => {
                      userColl.add({
                        username: username.toLowerCase(),
                        email: mail.toLowerCase(),
                        password: password,
                        uid: credentials.user.uid,
                      });

                      setUsername('');
                      setMail('');
                      setPassword('');
                      setConfirmPassword('');
                    })
                    .catch((error) => {
                      console.log(error);
                      if (error.code === 'auth/invalid-email') {
                        setFormErrorMessage('Badly formatted email');
                      } else if (error.code === 'auth/weak-password') {
                        setFormErrorMessage(
                          'Password is too short (should be 6 characters)'
                        );
                      }
                    });
                } else {
                  setFormErrorMessage('email taken');
                }
              });
          } else {
            setFormErrorMessage('username taken');
          }
        });
    } else {
      setFormErrorMessage('please fill in all fields');
    }
  };
  const handleClose = () => {
    props.hideUserForm();
  };
  const handleUIMessage = (msg) => {
    props.showUIMessenger(msg);
  }
  return (
    <div className="user-form-inner">
      <div className="user-form-logo-container">
        <div className="user-form-logo">
          <CSSTransition
            in={formState === 'login'}
            unmountOnExit
            timeout={300}
            classNames={'user-form-logo1-inner'}
          >
            <div className="user-form-logo-inner">
              <LoginSvg width={70} />
            </div>
          </CSSTransition>
          <CSSTransition
            in={formState === 'signup'}
            unmountOnExit
            timeout={300}
            classNames={'user-form-logo2-inner'}
          >
            <div className="user-form-logo-inner">
              <SignupSvg width={70} />
            </div>
          </CSSTransition>
        </div>
      </div>
      <div className="container">
        <div className="user-form-login">
          <form
            onSubmit={
              formState === 'login'
                ? (e) => handleLoginSubmit(e)
                : (e) => handleSignupSubmit(e)
            }
          >
            <Input
              ref={inp}
              type="text"
              theme={formState === 'login' ? 'secondary' : 'primary'}
              placeholder="User123"
              label="Username"
              suffix="account_circle"
              onChange={(e) => setUsername(e.target.value)}
              value={username}
            />
            <CSSTransition
              in={formState === 'signup'}
              unmountOnExit
              timeout={300}
              classNames={'user-form-signup-input'}
            >
              <Input
                type="email"
                theme={formState === 'login' ? 'secondary' : 'primary'}
                placeholder="user@user.com"
                label="email"
                suffix="alternate_email"
                onChange={(e) => setMail(e.target.value)}
                value={mail}
              />
            </CSSTransition>
            <Input
              type={showPassword ? 'text' : 'password'}
              theme={formState === 'login' ? 'secondary' : 'primary'}
              label="password"
              suffix={showPassword ? 'visibility_off' : 'visibility'}
              suffixOnClick={() => setShowPassword(!showPassword)}
              onChange={(e) => setPassword(e.target.value)}
              value={password}
            />
            <CSSTransition
              in={formState === 'signup'}
              unmountOnExit
              timeout={300}
              classNames={'user-form-signup-input'}
            >
              <Input
                type={showPassword ? 'text' : 'password'}
                theme={formState === 'login' ? 'secondary' : 'primary'}
                label="confirm password"
                onChange={(e) => setConfirmPassword(e.target.value)}
                value={confirmPassword}
              />
            </CSSTransition>
            <CSSTransition
              in={formErrorMessage !== ''}
              unmountOnExit
              timeout={300}
              classNames="user-form-info-feedback"
            >
              <p className="user-form-info-feedback">{formErrorMessage}</p>
            </CSSTransition>
            <div className="user-form-button-group">
              <Button
                size="medium"
                theme={formState === 'login' ? 'secondary' : 'primary'}
              >
                {formState === 'login' ? 'login' : 'sign up'}
              </Button>
              <Button
                type="button"
                size="medium"
                theme="tertiary"
                onClick={() => {
                  handleClose();
                  setUsername('');
                  setMail('');
                  setPassword('');
                  setConfirmPassword('');
                }}
              >
                back
              </Button>
            </div>
          </form>
        </div>
        <div className="user-form-register-shortcut">
          <p>
            Haven't {formState === 'login' ? 'signed up' : 'logged in'} yet? do
            it here:
          </p>
          <Button
            size="medium"
            theme={formState === 'login' ? 'primary' : 'secondary'}
            onClick={() =>
              setFormState(formState === 'login' ? 'signup' : 'login')
            }
          >
            {formState === 'login' ? 'sign up' : 'login'}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default UserForm;
