import { auth } from '../utils/firebase';
import { useHistory } from 'react-router-dom';

import Navlink from './Navlink';
import Button from './Button';

const Navbar = (props) => {
  let history = useHistory();

  const openUserForm = (form) => {
    props.showUserForm(form);
  };
  const signOut = () => {
    auth
      .signOut()
      .then(() => {
        console.log(props.authState);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const goToProfile = () => {
    console.log('test');
    history.push('/profile');
  };
  return (
    <div className="navbar">
      <div className="navbar-left"></div>
      <div className="navbar-center">
        <Navlink to="/">home</Navlink>
        <Navlink to="/games">games</Navlink>
      </div>
      <div className="navbar-right">
        <Button
          theme={props.authState ? 'primary' : 'secondary'}
          onClick={
            props.authState ? () => goToProfile() : () => openUserForm('login')
          }
        >
          {props.authState ? 'profile' : 'login'}
        </Button>
        <Button
          theme={props.authState ? 'tertiary' : 'primary'}
          onClick={
            props.authState ? () => signOut() : () => openUserForm('signup')
          }
        >
          {props.authState ? 'log out' : 'sign up'}
        </Button>
      </div>
    </div>
  );
};

export default Navbar;
