import React, { useEffect } from 'react';

const UIMessenger = (props) => {
  useEffect(() => {
    const handleHideUIMessenger = () => {
        props.hideUIMessenger(props.message);
    }

    setTimeout(() => {
        handleHideUIMessenger();
    }, 3000);
  }, [props]);

  return (
    <>
      <p>{props.message}</p>
    </>
  );
};

export default UIMessenger;
