import React, { useState, useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
import Home from './components/Home';
import Games from './components/Games';
import { auth } from './utils/firebase';

import './App.css';

import Navbar from './components/Navbar';
import Input from './components/Input';
import Button from './components/Button';
import Link from './components/Link';
import Dialog from './components/Dialog';
import LoginPopup from './components/LoginPopup';
import UserForm from './components/UserForm';
import UIMessenger from './components/UIMessenger';

function App() {
  const [loginPopupState, setLoginPopupState] = useState(false);
  const [userFormState, setUserFormState] = useState({
    display: false,
    currentForm: 'login',
  });
  const [UIMessengerState, setUIMessengerState] = useState({
    display: false,
    message: '',
  });

  const [authUser, setAuthUser] = useState(auth.currentUser);

  useEffect(() => {
    if (
      window.matchMedia &&
      window.matchMedia('(prefers-color-scheme: dark)').matches
    ) {
      document.body.setAttribute('darkmode', '');
    }
    if (authUser === null) {
      setLoginPopupState(true);
    } else if (authUser !== null) {
      setLoginPopupState(false);
    }

    const unsubscribe = auth.onAuthStateChanged((user) => setAuthUser(user));
    return () => {
      unsubscribe();
    };
  }, [authUser]);

  return (
    <div className="App">
      <Navbar
        authState={authUser}
        showUserForm={(form) =>
          setUserFormState({ display: true, currentForm: form })
        }
      />

      <Input type="range" theme="primary" placeholder="test" label="label" />
      <Input
        type="email"
        theme="secondary"
        placeholder="test"
        label="label"
        suffix="account_circle"
        suffixOnClick={() => console.log('suffix clicked')}
      />

      <Button
        theme="primary"
        onClick={() => {
          console.log(authUser);
        }}
      >
        Small button
      </Button>
      <Button
        onClick={() =>
          document.body.hasAttribute('darkmode')
            ? document.body.removeAttribute('darkmode')
            : document.body.setAttribute('darkmode', '')
        }
        theme="secondary"
      >
        Toggle darkmode
      </Button>

      <Link url="https://www.vg.no/">dette er en link</Link>

      <Dialog
        classNames="login-popup"
        timeout={1000}
        open={loginPopupState}
        close={() => setLoginPopupState(false)}
      >
        <LoginPopup
          showUserForm={(form) =>
            setUserFormState({ display: true, currentForm: form })
          }
          closeLoginPopup={() => setLoginPopupState(false)}
        />
      </Dialog>
      <Dialog
        classNames="user-form"
        open={userFormState.display}
        close={() => setUserFormState({ display: false })}
      >
        <UserForm
          currentForm={userFormState.currentForm}
          hideUserForm={() => setUserFormState({ display: false })}
          showUIMessenger={(msg) => setUIMessengerState({display: true, message: msg})}
        />
      </Dialog>
      <Dialog
        classNames="ui-messenger"
        open={UIMessengerState.display}
        close={() => setUIMessengerState({ display: false })}
        timeout={300}
      >
        <UIMessenger
          hideUIMessenger={(msg) => setUIMessengerState({ display: false, message: msg})}
          message={UIMessengerState.message}
        />
      </Dialog>

      <Switch>
        <Route path="/profile">
          <h1>profile</h1>
        </Route>
        <Route path="/games">
          <Games />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
