function Button(props) {
  return (
    <button
      className={`button ${
        props.size === 'large'
          ? 'button-large'
          : props.size === 'medium'
          ? 'button-medium'
          : 'button-small'
      } ${
        props.theme === 'primary'
          ? 'button-primary'
          : props.theme === 'secondary'
          ? 'button-secondary'
          : props.theme === 'tertiary'
          ? 'button-tertiary' 
          : ''
      }`}
      onClick={props.onClick}
      type={props.type}
    >
      {props.children}
    </button>
  );
}

export default Button;