import React, { useState } from 'react';
import generateInputId from '../utils/generateId';

const Input = React.forwardRef((props, ref) => {
  const [value, setValue] = useState(
    props.value !== undefined ? props.value : ''
  );
  const [checked, setChecked] = useState(
    props.checked !== undefined ? true : false
  );
  const id = generateInputId('input-generated-id-');
  if (props.type === 'checkbox') {
    return (
      <div className="input-container">
        <input
          type="checkbox"
          checked={checked}
          onChange={() => setChecked(!checked)}
          onClick={props.onClick}
        ></input>
      </div>
    );
  } else if (props.type === 'range') {
    return (
      <div className="input-container">
        <input
          type="range"
          value={value}
          onChange={(e) => setValue(Number(e.target.value))}
          onClick={props.onClick}
        ></input>
      </div>
    );
  }
  return (
    <div className="input-container" type={props.type}>
      {props.prefix !== undefined ? (
        <span onClick={props.prefixOnClick} className="input-icon">
          <span className="material-icons">{props.prefix}</span>
        </span>
      ) : (
        ''
      )}
      <div
        className={`input-container-inner ${
          props.theme === 'primary'
            ? 'input-container-inner-primary'
            : props.theme === 'secondary'
            ? 'input-container-inner-secondary'
            : ''
        } ${props.className}`}
        type={props.type}
        {...(props.label !== undefined ? { label: '' } : {})}
      >
        <input
          ref={ref}
          type={props.type}
          placeholder={
            props.placeholder !== undefined ? props.placeholder : ' '
          }
          value={props.value}
          onChange={props.onChange}
          onClick={props.onClick}
          id={id}
        ></input>
        {props.label !== undefined ? (
          <label htmlFor={id}>{props.label}</label>
        ) : (
          ''
        )}
      </div>
      {props.suffix !== undefined ? (
        <span onClick={props.suffixOnClick} className="input-icon">
          <span className="material-icons">{props.suffix}</span>
        </span>
      ) : (
        ''
      )}
    </div>
  );
});

export default Input;
