import Button from './Button';

import { ReactComponent as UnlockSvg } from '../assets/unlock.svg';

const LoginPopup = (props) => {
  const closeLoginPopup = () => {
    props.closeLoginPopup();
  };
  const showUserForm = (form) => {
    props.showUserForm(form);
  };
  return (
    <div className="login-popup-inner">
      <div className="login-popup-inner-info">
        <p>You are not logged in / signed up</p>
        <div className="login-popup-inner-svg">
          <UnlockSvg width={70}/>
        </div>
      </div>
      <div className="container">
        <Button
          onClick={() => {
            closeLoginPopup();
            showUserForm('login');
          }}
          theme="secondary"
        >
          Login
        </Button>
        <Button
          onClick={() => {
            closeLoginPopup();
            showUserForm('signup');
          }}
          theme="primary"
        >
          Sign up
        </Button>
      </div>
    </div>
  );
};

export default LoginPopup;
